<template>
  <a v-bind:aria-label="event.title + eventStartTime.format('MMMM D h:mm a z')" class="event" v-bind:href="'https://www.twitch.tv/events/' + event._id">
    <img v-bind:src="event.cover_image_url.replace('{width}', '640').replace('{height}', '360')"/>
    <div class="pink-circle date">
      <div class="day">{{eventStartTime.format('DD')}}</div>
      <div class="weekday-year">
        <span class="weekday">{{eventStartTime.format('dddd')}}<span v-if="isMultiDay">&nbsp;-&nbsp;</span><span v-if="isMultiDay" class="day-multi">{{eventEndTime.format('D')}}&nbsp;</span><span v-if="isMultiDay">{{eventEndTime.format('ddd')}}</span></span>
        <span v-if="!isMultiDay">{{eventStartTime.format('MMMM YYYY')}}</span>
        <span v-if="isMultiMonth" class="multi-month"><span>{{eventStartTime.format('MMM YY')}}</span><span>{{eventEndTime.format('MMM YY')}}</span></span>
      </div>
    </div>
    <div class="pink-circle title">
      <span>{{event.title}}</span>
    </div>
    <div class="pink-circle time" v-bind:class="{ live: isLive }">
        <span v-if="isLive">LIVE NOW</span>
        <span class="schdeuled" v-if="!isLive && isMultiDay">{{eventStartTime.format('ddd h:mma')}} - {{eventEndTime.format('ddd h:mma z')}}</span>
        <span class="schdeuled" v-if="!isLive && !isMultiDay">{{eventStartTime.format('h:mma')}} - {{eventEndTime.format('h:mma z')}}</span>
    </div>
  </a>
</template>

<script>
import StreamStatus from '../global/StreamStatus'
import MomentTZ from 'moment-timezone'
export default {
  name: 'event',
  props: ['event'],
  components: {StreamStatus},
  computed: {
    eventStartTime: function () {
      return MomentTZ.tz(this.event.start_time, MomentTZ.tz.guess())
    },
    eventEndTime: function () {
      return MomentTZ.tz(this.event.end_time, MomentTZ.tz.guess())
    },
    isLive: function () {
      var now = MomentTZ.tz()
      return now.isAfter(this.eventStartTime) && now.isBefore(this.eventEndTime)
    },
    isMultiDay: function () {
      return this.eventStartTime.dayOfYear() !== this.eventEndTime.dayOfYear()
    },
    isMultiMonth: function () {
      return this.isMultiDay && (this.eventStartTime.month() !== this.eventEndTime.month())
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.event{
  display: inline-block;
  margin: $standard-spacing;
  color: $font-white;
  width: 250px;
  vertical-align: top;
  img {
    width: 100%;
    transition: $standard-transition;
  }
  span {
    display: block;
  }
  .day {
    font-size: 33px;
    margin-right: $standard-spacing;
  }
  .day-multi {
    color: $font-white;
  }
  .weekday{
    color: $light-purple;
    transition: $standard-transition;
    font-size: 14px;
    span{
      display: inline-block;
    }
  }
  .multi-month {
    display: flex;
    justify-content: space-between;
    span {
      display: inline-block;
    }
  }
  .pink-circle {
    display: block;
    position: relative;
    border-radius: 50px;
    padding: 5px;
    color: $font-white;
    border: 1px solid $light-purple;
    margin: $standard-spacing $standard-spacing-half;
    min-height: $standard-spacing3;
    transition: $standard-transition;
    div {
      display: inline-block;
    }
    &.title{
      display: flex;
      justify-content: center;
      flex-direction: column;
      transition: $standard-transition;
      span{
        display: inline-block;
      }
    }
    &.time {
      min-height: $standard-spacing2;
      white-space: nowrap;
      border-color: $font-gray;
      background-color: $font-gray-light;
      font-size: 16px;
      color: $dark-purple;
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &.live{
        background-color: $light-purple;
        border-color: $light-purple;
        color: $font-white;
      }
    }
  }
  &:hover, &:focus {
    img, .live{
      transform: scale(1.1);
    }
    .date, .title{
      background-color: $light-purple;
      .weekday{
        color: $font-white;
      }
    }
  }
}
</style>



