<template>
  <main id="container">
    <div class="wrapper" v-if="showGame" v-on:click="stopGame">
    </div>
  </main>
</template>

<script>
  import Game from './js/game-controller.js'

  export default {
    name: 'game',
    data () {
      return {
        showGame: false,
        cursor: 0,
        winningCombo: [38, 38, 40, 40, 37, 39, 37, 39, 66, 65]
      }
    },
    methods: {
      runGame () {
        this.showGame = true
        Game.Run()
      },
      checkKey (e) {
        if (e.keyCode === this.winningCombo[this.cursor]) {
          this.cursor++
          if (this.cursor === this.winningCombo.length) {
            this.runGame()
            this.cursor = 0
          }
        } else {
          this.cursor = 0
        }
        if (this.showGame && e.keyCode === 27) {
          this.stopGame()
        }
      },
      stopGame () {
        this.showGame = false
        var event = new Event('closeGame')
        dispatchEvent(event)
      }
    },
    created () {
      window.addEventListener('keyup', this.checkKey)
    }
}
</script>
<style scoped lang="scss">
.wrapper{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(0, 0, 0, 0.95);
}
</style>



