<template>
  <main class="calendar-page">
    <div class="calendar-intro" id="main" tabindex="0">
      <h2 class="section-header">Regular Stream</h2>
      <p class="stream-times"><stream-times></stream-times></p>
      <p>Stream is <stream-status></stream-status></p>
    </div>
    <div class="calendar-body">
      <h2><a class="link-btn" href="https://www.twitch.tv/lowco/events">Special Events</a></h2>
      <transition name="div-fade" mode="out-in">
        <div v-if="!loading">
          <event v-if="events.length > 0" v-for="event in events" :key="event.$index" :event="event"></event>
          <p class="no-events" v-if="events.length === 0">No events planned, check back later!</p>
        </div>
        <loading-spinner v-else></loading-spinner>
      </transition>
    </div>
  </main>
</template>

<script>
import StreamStatus from './global/StreamStatus'
import StreamTimes from './global/StreamTimes'
import LoadingSpinner from './global/Loading'
import Event from './calendar/Event'
import TwitchGateway from './../gateways/twitch.js'

export default {
  name: 'calendar',
  data () {
    return {
      events: [],
      loading: true
    }
  },
  computed: {
  },
  components: {StreamStatus, Event, StreamTimes, LoadingSpinner},
  mounted () {
    TwitchGateway.getEvents(52925091).then(function (results) {
      this.events = results.body.events
      this.loading = false
    }.bind(this))
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.events-wrapper{
  h3{
    display: inline-block;
    vertical-align: top;
    margin: 0;
  }
}
.stream-times{
  font-size: 2vw;
  margin-top: $standard-spacing;
  padding-bottom: $standard-spacing-half;
}
.calendar-body{
  background-color: $dark-purple;
  color: $font-white;
  text-align: center;
  padding: $home-section-padding;
  flex: 1 0 auto;
  h2 {
    margin-bottom: $standard-spacing2;
  }
}
.calendar-intro{
  padding: $home-section-padding;
  height: 162px;
  margin: 0 auto;
  p{
    a{
      color: $font-gray;
      &:hover{
        color: $font-white;
      }
    }
  }
}
.no-events {
  margin-top: 35px;
  color: $font-white;
  font-size: 1.5vw;
}
@media (max-width: 1200px) {
  .stream-times{
    font-size: 3vw;
  }
  .no-events {
    font-size: 2vw;
  }
}
@media (max-width: 1000px) {
  .stream-times{
    font-size: 4vw;
  }
  .no-events {
    font-size: 3vw;
  }
}
@media (max-width: 700px) {
  .stream-times{
    font-size: 5vw;
  }
  .no-events {
    font-size: 4vw;
  }
}
@media (max-width: 500px) {
  .stream-times{
    font-size: 5.15vw;
  }
  .no-events {
    font-size: 4.5vw;
  }
}
</style>



