<template>
  <div class="youtube">
    <div class="wrap">
      <h2><a class="link-btn" href="https://www.youtube.com/user/Lowco2525">My Youtube</a></h2>
      <transition name="div-fade" mode="out-in">
        <div class="video-wrapper" v-if="!loading">
          <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[0].contentDetails.videoId)" frameborder="0" allowfullscreen></iframe></div>
          <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[1].contentDetails.videoId)" frameborder="0" allowfullscreen></iframe></div>
          <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[2].contentDetails.videoId)" frameborder="0" allowfullscreen></iframe></div>
        </div>
        <loading-spinner class="loading" v-else></loading-spinner>
      </transition>
    </div>
  </div>
</template>

<script>
import YoutubeGateway from '../../gateways/youtube.js'
import LoadingSpinner from '../global/Loading'
export default {
  name: 'youtube',
  components: {LoadingSpinner},
  data () {
    return {
      youtubeList: [{id: ''}, {id: ''}, {id: ''}],
      loading: true
    }
  },
  methods: {
    getPlaylistString (targetId) {
      return targetId === '' ? '' : 'https://www.youtube.com/embed/' + targetId + '?playlist=PL00vBu4kkMvy-uNdO99o2RA9Aqz7G5Aw2&version=3'
    }
  },
  created () {
    YoutubeGateway.getPlaylists().then(function (results) {
      this.youtubeList = results.body.items
      this.loading = false
    }.bind(this))
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.youtube{
  padding: $home-section-padding;
  background-color: $dark-purple;
  color: $font-gray;
  text-align: left;
  .loading{text-align: center}
  .wrap {
    max-width: 1100px;
    margin: auto;
  }
  h2{
    margin-top: 6px;
  }
}

.video-wrapper {
  position: relative;
  justify-content: space-between;
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  text-align: center;
}

.iframe-wrap {
  position: relative;
  min-width: 200px;
  width: 32%;
}

.iframe-wrap:before{
  content: "";
  display: inline-block;
  padding-top: 55.75%; /* 16:9 */
}

.iframe-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 750px) {
  .video-wrapper{
    flex-flow: column;
  }
  .iframe-wrap {
    margin: $standard-spacing auto 0 auto;
    width: 65%;
  }
  .youtube {
    text-align: center;
  }
}
</style>



