<template>
  <div class="blog-post">
  	<h3 class="blog-title">{{title}}</h3>
  	<icon name="clock-o" class="icon"></icon><span>{{age}}</span>
    <div v-show="labels" class="tags">Tags:<div class="labels-wrap"> <button v-for="label in labels" v-bind:class="{ selected: selectedLabel === label }" class="labels" v-on:click="sortList(label)" :aria-label="'order by ' + label">{{label}}</button></div></div>
  	<div v-html="styled" class="blog-clear"></div>
  </div>
</template>

<script>
export default {
  name: 'blogpost',
  props: ['title', 'age', 'description', 'labels', 'selectedLabel'],
  data () {
    return {
      isExpanded: false
    }
  },
  methods: {
    expand: function () {
      this.isExpanded = !this.isExpanded
    },
    sortList: function (label) {
      if (this.selectedLabel === label) {
        label = ''
      }
      this.$emit('sort', label)
    }
  },
  computed: {
    styled: function () {
      var reg = /background-color.*?;|font-family.*?;/g
      var imgReg = /;"><img/g
      var temp = this.description.replace(reg, '')
      return temp.replace(imgReg, '; max-width: 100%;"><img')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../../variables.scss';
.blog-post {
	background-color: $font-white;
  max-width: 700px;
  border-radius: 15px;
  transition: $standard-transition;
  margin: 0 auto $standard-spacing2 auto;
  padding: $standard-spacing3 $standard-spacing4;
  text-align: left;
  .icon {
    fill: $font-gray;
    vertical-align: bottom;
    margin-right: $standard-spacing-half;
  }
  .blog-title {
    color: $dark-purple;
    font-size: 2.25em;
    margin: 0 0 $standard-spacing-half 0;
  }
  .blog-clear {
    text-align: initial;
    margin-top: $standard-spacing;
    position: relative;
    word-wrap: break-word;
    &:after{
      content: '';
      clear: both; 
      display: block;
    }
    a{
      text-decoration: underline !important;
    }
    iframe, object embed{
      width: 67vw;
      max-width: 560px;
      height: 38vw;
      max-height: 316px;
    }
    img{
      height: auto;
      max-width: 89%;
    }
  }
  .tags{
    margin-top: $standard-spacing-half;
  }
  .labels{
    border: 2px solid $light-purple;
    background: none;
    border-radius: 25px;
    margin: -$standard-spacing-half/2 0 $standard-spacing-half  $standard-spacing-half/2;
    transition: $standard-transition;
    font-family: inherit;
    letter-spacing: inherit;
    color: $font-gray;
    &:hover, &:focus, &.selected{
      cursor: pointer;
      background-color: $light-purple;
      color: $font-white;
    }
    &-wrap{
      display: inline-block;
      width: calc(100% - 36px);
      vertical-align: top;
    }
  }
}
</style>



