<template>
  <div class="social">
    <h2 class="section-header">Social Media</h2>
    <p>Check my social media links, follow me to stay updated</p>
    <a href="https://twitter.com/lowco2525" title="twitter" target="_blank" class="link-wrap">
      <icon name="twitter" class="icon" :scale="iconSize+0.25"></icon>
      <label>Twitter</label>
      <span>{{twitterCount}} followers</span>
    </a>
    <a href="https://discord.gg/Lowco" title="discord" target="_blank" class="link-wrap">
      <icon name="discord" class="icon" :scale="iconSize"></icon>
      <label>Discord</label>
      <span>{{discordCount}} members</span>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank" class="link-wrap">
      <icon name="youtube-play" class="icon" :scale="iconSize+0.25"></icon>
      <label>Youtube</label>
      <span>{{youtubeCount}} subscribers</span>
    </a>
    <a href="https://www.instagram.com/lowco2525/" title="instagram" target="_blank" class="link-wrap">
      <icon name="instagram" class="icon" :scale="iconSize"></icon>
      <label>Instagram</label>
      <span>{{instagramCount}} followers</span>
    </a>
    <a href="https://www.twitch.tv/lowco" title="twitch" target="_blank" class="link-wrap">
      <icon name="twitch" class="icon" :scale="iconSize"></icon>
      <label>Twitch</label>
      <span>{{twitchCount}} followers</span>
    </a>
  </div>
</template>

<script>
import SocialMediaIcons from '../header/SocialMediaIcons'
import twitter from '../../gateways/twitter'
import instagram from '../../gateways/instagram'
import youtube from '../../gateways/youtube'
import twitch from '../../gateways/twitch'
export default {
  name: 'twitch',
  components: {SocialMediaIcons},
  methods: {
    formatNumber: function (num) {
      return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }
  },
  created: function () {
    twitter.getUser().then(function (results) {
      this.twitterCount = this.formatNumber(results.body.followers_count)
    }.bind(this))
    instagram.getUserProfile().then(function (results) {
      this.instagramCount = this.formatNumber(results.body.user.followed_by.count)
    }.bind(this))
    youtube.getSubscribers().then(function (results) {
      this.youtubeCount = this.formatNumber(results.body.items[0].statistics.subscriberCount)
    }.bind(this))
    twitch.getChannelInfo(52925091).then(function (results) {
      this.twitchCount = this.formatNumber(results.body.followers)
    }.bind(this))
  },
  data () {
    return {
      iconSize: 3,
      twitterCount: '10,000',
      youtubeCount: '7,000',
      instagramCount: '3,000',
      discordCount: '1,600',
      twitchCount: '85,000'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
  .social{
    padding: $home-section-padding;
  }
  .icon{
    display: block;
    fill: $light-purple;
    transition: $standard-transition;
    margin: 0 auto;
  }
  .link-wrap{
    display: inline-block;
    padding: $standard-spacing;
    text-align: center;
    border-radius: 20px;
    margin: $standard-spacing;
    transition: $standard-transition;
    label{
      color: $font-gray;
      transition: $standard-transition;
    }
    span{
      display: block;
      color: $light-purple;
      margin-top: 2px;
    }
    &:hover, &:focus{
      background-color: $light-purple;
      transform: scale(1.1);
      label, span{
        color: $font-white;
      }
      .icon{
        fill: $font-white;
      }
    }
  }
</style>



