<template>
  <a href="https://www.twitch.tv/lowco" v-bind:class="{ online: streamStatus === 'ONLINE' }" class="link-btn">{{streamStatus}}</a>
</template>

<script>
import TwitchGateway from '../../gateways/twitch.js'
export default {
  name: 'stream-status',
  data () {
    return {
      streamStatus: 'OFFLINE'
    }
  },
  mounted () {
    TwitchGateway.isStreamLive(52925091).then(function (results) {
      if (results.body.stream !== null) {
        this.streamStatus = 'ONLINE'
      }
    }.bind(this))
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.stream-status{
  margin: 9px 0;
}
a{
  border: 2px solid $stream-offline;
  border-radius: 25px;
  &.online{
    border: 2px solid $stream-online;
    &:hover, &:focus{
      background-color: $stream-online;
    }
  }
  &:hover, &:focus{
    background-color: $stream-offline;
  }
}
</style>



