<template>
  <div class="blog-list" v-bind:ref="'blogList'">
    <transition name="div-fade" mode="out-in">
      <div v-if="!loading">
        <div class="tags-wrap">
          <p>Filters</p>
          <button v-for="tag in predefinedTags" v-bind:class="{ selected: filterTag === tag }" class="tags" v-on:click="filterByTag(tag)" :aria-label="'filter by ' + tag">{{tag}}</button>
        </div>
        <blog-post :title="blog.title" :age="parseDate(blog.published)" :description="blog.content" v-for="blog in displayedBlogs" :key="blog.$index" :labels="blog.labels" :selectedLabel="selectedLabel" v-on:sort="sortByLabel"></blog-post>
        <p class="no-posts" v-if="orderedBlogs.length === 0">No posts found, check back later!</p>
        <blog-pagination :total="this.orderedBlogs.length" @clicked="paginationClick"></blog-pagination>
      </div>
      <loading-spinner v-else></loading-spinner>
    </transition>
  </div>
</template>

<script>
import BlogPost from './BlogPost'
import BlogPagination from './BlogPagination'
import api from '../../gateways/blogger'
import MomentTZ from 'moment-timezone'
import LoadingSpinner from '../global/Loading'
export default {
  name: 'bloglist',
  components: {BlogPost, BlogPagination, LoadingSpinner},
  methods: {
    filterList: function (filter) {
      var index = 0
      for (var i = 0; i < this.orderedBlogs.length; i++) {
        if (this.orderedBlogs[i].labels) {
          for (var j = 0; j < this.orderedBlogs[i].labels.length; j++) {
            if (this.orderedBlogs[i].labels[j].indexOf(filter) !== -1) {
              index++
              break
            }
          }
        }
      }
      this.orderedBlogs = this.orderedBlogs.slice(0, index)
    },
    orderList: function (blogLabel) {
      this.orderedBlogs.sort(function (a, b) {
        if (a.labels && !b.labels) {
          if (a.labels.indexOf(blogLabel) !== -1) {
            return -1
          }
        }
        if (!a.labels && b.labels) {
          if (b.labels.indexOf(blogLabel) !== -1) {
            return 1
          }
        }
        if (a.labels && b.labels) {
          var aIndex = a.labels.indexOf(blogLabel)
          var bIndex = b.labels.indexOf(blogLabel)
          if (aIndex !== -1 && bIndex !== -1) {
            a = new Date(a.published)
            b = new Date(b.published)
            return a > b ? -1 : a < b ? 1 : 0
          }
          return bIndex - aIndex
        }
        return 0
      })
    },
    orderLabels: function (inputLabel) {
      this.displayedBlogs.forEach(function (blog) {
        if (blog.labels) {
          blog.labels.sort(function (x, y) {
            return x === inputLabel ? -1 : y === inputLabel ? 1 : 0
          })
        }
      })
    },
    sortByLabel: function (label) {
      this.filterTag = ''
      if (label === '') {
        label = 'pinned'
      }
      this.selectedLabel = label
      this.orderedBlogs = this.blogs.slice(0)
      this.orderList(label)
      this.offset = 0
      this.populateDisplay()
      this.focusTopOfList()
    },
    filterByTag: function (tag) {
      if (tag === this.filterTag) {
        this.sortByLabel('')
        this.filterTag = ''
      } else {
        this.filterTag = tag
        this.selectedLabel = 'pinned'
        this.orderedBlogs = this.blogs.slice(0)
        this.orderList(tag)
        this.offset = 0
        this.filterList(tag)
        this.populateDisplay()
        this.focusTopOfList()
      }
    },
    focusTopOfList: function () {
      this.$refs['blogList'].scrollIntoView()
      this.$refs['blogList'].setAttribute('tabindex', 0)
      this.$refs['blogList'].focus()
      this.$refs['blogList'].removeAttribute('tabindex')
    },
    parseDate: function (dateStr) {
      return MomentTZ.tz(dateStr, MomentTZ.tz.guess()).format('MMM D, YYYY')
    },
    populateDisplay: function () {
      this.displayedBlogs = this.orderedBlogs.slice(this.offset, this.offset + 3)
      this.orderLabels(this.selectedLabel)
    },
    paginationClick: function (newPosition) {
      this.offset = newPosition * 3
      this.populateDisplay()
      this.focusTopOfList()
    }
  },
  data () {
    return {
      offset: 0,
      selectedLabel: 'pinned',
      filterTag: '',
      predefinedTags: ['Achievements', 'Charity', 'Events', 'Promotions'],
      displayedBlogs: [],
      orderedBlogs: [],
      blogs: [],
      loading: false
    }
  },
  created: function () {
    this.loading = true
    api.getBlogPosts().then(function (results) {
      this.blogs = results.body.items
      this.orderedBlogs = this.blogs.slice(0)
      this.orderList(this.selectedLabel)
      this.populateDisplay()
      this.loading = false
    }.bind(this))
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-list{
  background-color: $dark-purple;
  padding: $home-section-padding;
  flex: 1 0 auto;
}
.tags{
  border: 2px solid $light-purple;
  background: none;
  border-radius: 25px;
  transition: $standard-transition;
  font-family: inherit;
  letter-spacing: inherit;
  color: $font-white;
  margin: 10px;
  width: 125px;
  padding: 10px;
  &:hover, &:focus, &.selected{
    cursor: pointer;
    background-color: $light-purple;
  }
  &-wrap{
    display: flex;
    max-width: 700px;
    margin: -$standard-spacing auto $standard-spacing2 auto;
    justify-content: space-between;
    color: $font-white;
    p{
    }
  }
}
.no-posts {
  margin-top: 35px;
  color: $font-white;
  font-size: 1.5vw;
}
@media (max-width: 1200px) {
  .no-posts {
    font-size: 2vw;
  }
}
@media (max-width: 1000px) {
  .no-posts {
    font-size: 3vw;
  }
}
@media (max-width: 700px) {
  .no-posts {
    font-size: 4vw;
  }
  .tags-wrap {
    p {
      display: none;
    }
  }
}
@media (max-width: 500px) {
  .no-posts {
    font-size: 4.5vw;
  }
  .tags-wrap {
    .tags{
      padding: 5px;
      margin: 0 2px;
    }
  }
}
</style>



