<template>
  <main class="error-page">
    <div class="error-intro" id="main" tabindex="0">
      <h2 class="section-header">Error 404</h2>
      <p>Something went wrong<br/>We couldn't find what you were looking for</p>
    </div>
    <div class="error-body">
      <img class='logo' src="../assets/graphics/404.png" alt="404 Error"/>
      <router-link to="/" title="Lowco.tv" class="link-btn">Back Home</router-link>
    </div>
  </main>
</template>

<script>

export default {
  name: 'ErrorPage'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.error-body{
  background-color: $dark-purple;
  text-align: center;
  padding-bottom: 100px;
  flex: 1 0 auto;
  img{
    display: block;
    margin: 0 auto;
    width: 50%;
  }
}
.error-intro{
  padding: $home-section-padding;
  max-width: 600px;
  margin: 0 auto;
}
</style>



