import Vue from 'vue'
var userId = 'Lowco2525'

export default {
  getUserProfile (channelId) {
    var url = 'https://zzv2ngo0k1.execute-api.us-west-2.amazonaws.com/prod/instagram/getuser?target=' + userId
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



