<template>
  <div class="blog-pagination" v-show="total > 0">
    <button v-on:click="advance(-1)" class="next-prev" :class="{ disabled: disablePrev }" v-bind="{disabled: disablePrev}" aria-label="previous"><icon name="chevron-left" class="icon"></icon></button>
    <button v-on:click="onClickButton(n)" v-for="n in pages" v-bind:class="{ selected: position === n }" v-bind="{'aria-label': getLabel(n)}">{{n}}</button>
    <button v-on:click="advance(1)" class="next-prev" :class="{ disabled: disableNext }" v-bind="{disabled: disableNext}"><icon name="chevron-right" class="icon" aria-label="next"></icon></button>
  </div>
</template>

<script>
export default {
  name: 'blogpagination',
  props: ['total'],
  methods: {
    advance (direction) {
      if ((this.position + direction > 0) && (this.position + direction <= this.pages)) {
        this.onClickButton(this.position + direction)
      }
    },
    onClickButton (newPosition) {
      this.position = newPosition
      this.$emit('clicked', newPosition - 1)
    },
    getLabel (position) {
      return 'page ' + position
    }
  },
  computed: {
    pages: function () {
      return Math.ceil(this.total / 3)
    },
    disableNext: function () {
      return this.position === this.pages
    },
    disablePrev: function () {
      return this.position === 1
    }
  },
  watch: {
    'total': function () {
      this.position = 1
    }
  },
  data () {
    return {
      position: 1
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
button{
  background-color: $dark-purple;
  color: $font-white;
  border: solid 1px $light-purple;
  border-radius: 25px;
  height: $standard-spacing3;
  width: $standard-spacing3;
  margin: $standard-spacing-half;
  font-family: $font-stack;
  font-weight: 100;
  cursor: pointer;
  vertical-align: middle;
  &.next-prev{
    background-color: $light-purple;
    &.disabled{
      background-color: $dark-purple;
    }
  }
}
button:hover, button:focus, button.selected{
  background-color: $light-purple;
}
.icon{
  vertical-align: middle;
}
</style>



