<template>
  <div class="newsletter">
    <h2 class="section-header">Subscribe to my newsletter</h2>
    <p>This section is in flux until I can get a mailchimp embed link Text here Text here Text here Text here Text here Text here Text here Text here </p>
    <!-- Begin MailChimp Signup Form -->
    <div id="mc_embed_signup">
      <form action="//twitch.us9.list-manage.com/subscribe/post?u=55b984b459e22d231b09a8dd4&amp;id=02a9f3245b" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" class="validate" target="_blank" novalidate>
        <div id="mc_embed_signup_scroll">
          <div class="indicates-required"><span class="asterisk">*</span> indicates required</div>
          <div class="mc-field-group">
            <label for="mce-EMAIL">Email Address  <span class="asterisk">*</span></label>
            <input type="email" value="" name="EMAIL" class="required email" id="mce-EMAIL">
          </div>
          <div class="mc-field-group">
            <label for="mce-TWITCHNAME">Twitch Name  <span class="asterisk">*</span></label>
            <input type="text" value="" name="TWITCHNAME" class="required" id="mce-TWITCHNAME">
          </div>
          <div class="mc-field-group input-group hidden" aria-hidden="true">
            <strong>Email Format </strong>
            <ul>
              <li><input type="radio" value="html" name="EMAILTYPE" id="mce-EMAILTYPE-0" checked="checked" ><label for="mce-EMAILTYPE-0">html</label></li>
              <li><input type="radio" value="text" name="EMAILTYPE" id="mce-EMAILTYPE-1"><label for="mce-EMAILTYPE-1">text</label></li>
            </ul>
          </div>
          <div id="mce-responses" class="clear">
            <div class="response" id="mce-error-response" style="display:none"></div>
            <div class="response" id="mce-success-response" style="display:none"></div>
          </div>    <!-- real people should not fill this in and expect good things - do not remove this or risk form bot signups-->
          <div style="position: absolute; left: -5000px;" aria-hidden="true"><input type="text" name="b_55b984b459e22d231b09a8dd4_02a9f3245b" tabindex="-1" value=""></div>
          <div class="clear">
          <input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" class="button"></div>
        </div>
      </form>
    </div>
    <!--End mc_embed_signup-->
    <link href="//cdn-images.mailchimp.com/embedcode/classic-10_7.css" rel="stylesheet" type="text/css">
  </div>
</template>

<script>
/*eslint-disable*/
import Validate from './js/mc-validate.js'

export default {
  name: 'newsletter',
  created: function () {
    (function($) {window.fnames = new Array(); window.ftypes = new Array();fnames[0]='EMAIL';ftypes[0]='email';fnames[4]='TWITCHNAME';ftypes[4]='text';}(jQuery));var $mcj = jQuery.noConflict(true);
  }
}
/*eslint-enable */
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';

.newsletter{
  padding: $home-section-padding;
  max-width: 500px;
  margin: 0 auto;
}
.hidden{
  display: none;
}
#mc_embed_signup .asterisk {top: 2px; color: $light-purple;}
#mc_embed_signup{background:$font-white; clear:left; font:14px; }
#mc_embed_signup .mc-field-group input {
  font-family: "coolvetica", Helvetica, Arial, sans-serif;
  background:$font-white;
  letter-spacing: 1px;
  color: #585a61;
  font-size: .85em;
  box-sizing: border-box;
  line-height: 1.5;
  border-radius: 15px;
  &:focus{
    box-shadow: 0 0 0 1px $light-purple;
    outline: none;
  }
  &.error{
    box-shadow: 0 0 0 1px $live-red;
  }
}
#mc_embed_signup .button {
  border-radius: 15px;
  background-color: $dark-purple;
  &:hover, &:focus{
    background-color: $light-purple;
  }
}
</style>



