<template>
  <header class="nav">
    <div class="nav-top">
      <router-link to="/" title="Lowco.tv"><img class='logo' src="../assets/graphics/logo.png" alt="lowco-logo"/></router-link>
      <button class="skipToMain" v-on:click="skipToMain">Skip to Main Content</button>
      <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
    </div>
    <div class="nav-mid">
      <video ref="" :src="videoRef" id="video-container" width="100%" autoplay="autoplay" loop="loop"/>
      <div class="centered">
        <img class="logo-sm" src="../assets/graphics/logo-70x70.png" alt="lowco-logo"/>
        <h1>fulltime variety caster</h1>
        <a class="link-btn" href="https://www.twitch.tv/lowco" aria-label="Live streamer on twitch">Live Stream</a>
      </div>
      <div class="play-btn-container">
        <button class="btn" v-on:click="playPause" v-bind:class="{ pause: !videoPaused, play: videoPaused }" aria-label="play-pause the header video">
          <span class="bar bar-1"></span>
          <span class="bar bar-2"></span>       
        </button>
      </div>
    </div>
    <nav class="nav-bar">
      <router-link to="/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>home</div></router-link><!--
      --><router-link to="/calendar"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>calendar</div></router-link><!--
      --><router-link to="/portfolio"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>portfolio</div></router-link><!--
      --><a href="https://shop.spreadshirt.com/lowco2525/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>shop</div></a><!--
      --><a href="https://www.twitch.tv/lowco/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>stream</div></a><!--
      --><router-link to="/contact"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>contact</div></router-link>
    </nav>
  </header>
</template>

<script>
import SocialMediaIcons from './header/SocialMediaIcons'

export default {
  name: 'header-bar',
  components: {SocialMediaIcons},
  data () {
    return {
      videoPaused: false
    }
  },
  computed: {
    videoRef () { return 'http://' + window.location.host + '/static/videoheader.mp4' }
  },
  methods: {
    playPause: function () {
      var video = document.getElementById('video-container')
      if (video.paused) {
        video.play()
        this.videoPaused = false
      } else {
        video.pause()
        this.videoPaused = true
      }
    },
    skipToMain: function () {
      var main = document.getElementById('main')
      console.log(main)
      main.focus()
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
#video-container{
  margin-bottom: -4px;
}
.skipToMain {
  position: absolute;
  left: -99999px;
  top: 0;
  margin-left: 0;
  text-align: center;
  list-style: outside none;
  background: none;
  color: $font-white;
  border: none;
  &:focus{
    position: relative;
    display: inline-block;
    left: 0;
  }
}
.play-btn-container {
  position: absolute;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  margin-top: -25px;
}

.btn {
  position: relative;
  width: 23px;
  height: 23px;
  border: none;
  border-radius: 7px;
  background: none;
  padding: 0;
  cursor: pointer;
  margin-right: 5px;
  transition: border 0.1s ease-in-out;
  
  &:hover {
    border-color: $light-purple;
    .bar {
      background-color: $light-purple; 
    }
  }
  
  .bar {
    display: inline-block;
    position: absolute;
    top: 2px;
    left: 0;
    width: 3px;
    height: 15px;
    border-radius: 10px;
    background-color: $font-white;
    transform-origin: center;
    transition: transform 0.4s ease-in-out, background 0.1s ease-in-out;
  }
  
  &.pause {
    .bar-1 {
      transform: translateX(6px) translateY(2px) rotate(0deg);
    }
    .bar-2 {
      transform: translateX(14px) translateY(2px) rotate(0deg);
    }
  }
  
  &.play {
    .bar-1 {
      transform: translateX(10px) translateY(-1px) rotate(-55deg);
    }
    .bar-2 {
      transform: translateX(10px) translateY(6px) rotate(-125deg);
    }
  }
  
}
.nav{
  text-transform: uppercase;
  background-color: $dark-purple;
  padding-top: $standard-spacing2;
  border-bottom: 3px solid $light-purple;
  h1{
    color: $font-white;
    margin: 0 0 $standard-spacing 0;
  }
  .logo{
    width: 200px;
    vertical-align: bottom;
  }
  &-top{
    display: flex;
    justify-content: space-between;
    width: 90%;
    max-width: 1500px;
    margin: 0 auto;
    .social{
      display: inline-block;
      margin: 15px 0;
    }
  }
  &-mid{
    position: relative;
    .centered{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .logo-sm{
      border-radius: 70px;
    }
    video {
      width: 100%;
      object-fit: cover;
      min-height: 200px;
    }
  }
  &-bar{
    .icon{
      display: block;
      visibility: hidden;
      margin: 0 auto;
      fill: $font-white;
    }
    a{
      display: inline-flex;
      position: relative;
      flex-direction: column;
      padding: $standard-spacing3 0;
      color: $font-white;
      min-width: 100px;
      transition: $standard-transition;
      margin: 1px 1px 0 0;
      .nav-link-wrap{
        padding-bottom: 20px;
      }
      &~a {
        .nav-link-wrap:before {
          content:"";
          position: absolute;
          left: -1px;
          background: #1e1e1e;
          height: $standard-spacing4;
          width: 1px;
        }
      }
      &:hover,
      &:focus{
        background-color: $light-purple;
      }
    }
    .router-link-exact-active{
      background-color: $light-purple;
      a{
        color: $font-white;
      }
      .icon{
        visibility: visible;
      }
    }
  }
}
@media (max-width: 700px) {
  .nav-top {
    text-align: center;
    .social{
      margin-top: 0;
      width: 100%;
    }
    a{
      display: none;
    }
  }
  .nav-bar a {
    min-width: 0;
    padding: $standard-spacing 1.5%;
  }
  .nav-link-wrap{
    font-size: smaller;
  }
  .nav-mid .centered {
    width: 100%;
    img {
      display: block;
      margin: 0 auto;
    }
  }
}
</style>



