<template>
  <div class="twitch">
    <div class="twitch-description">
      <h2 class="title"><a class="link-btn" href="https://www.twitch.tv/lowco">My Twitch</a></h2>
      <p>I mostly play popular/new games for PC, PS4, and XB1.</p>
      <p class="no-margin-top">My schedule: <stream-times></stream-times></p>
      <p>Stream is <stream-status></stream-status></p>
    </div>
    <div class='twitch-stream'>
      <iframe src="https://player.twitch.tv/?channel=lowco&muted=true" frameborder="0" scrolling="no"></iframe>
    </div>
  </div>
</template>

<script>
import StreamStatus from '../global/StreamStatus'
import StreamTimes from '../global/StreamTimes'

export default {
  name: 'twitch',
  components: {StreamStatus, StreamTimes}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.twitch{
  display: flex;
  flex-flow: row-reverse;
  justify-content: center;
  padding: $home-section-padding;
  background-color: $dark-purple;
  color: $font-white;
  text-align: left;
}

.twitch-description {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  margin-left: $standard-spacing;
  .no-margin-top{
    margin-top: 0;
  }
  .title{
    margin: $standard-spacing 0 $standard-spacing-half 0;
  }
}

.twitch-stream {
  position: relative;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  min-width:350px;
  max-width: 540px;
  flex-basis: auto;
  flex-grow: 1;
  margin-right: $standard-spacing;
}

.twitch-stream:before{
  content: "";
  display: block;
  padding-top: 55.75%; /* 16:9 */
}

.twitch-stream iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 700px) {
  .twitch{
    flex-flow: row wrap;
  }
  .twitch-stream{
    margin: $standard-spacing 0 0 0;
  }
  .twitch-description {
    text-align: center;
  }
}
</style>



