<template>
  <div>
    <a href="https://twitter.com/lowco2525" title="twitter" target="_blank" aria-label="twitter">
      <icon name="twitter" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://discord.gg/Lowco" title="discord" target="_blank" aria-label="discord">
      <icon name="discord" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank" aria-label="youtube">
      <icon name="youtube" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.instagram.com/lowco2525/" title="instagram" target="_blank" aria-label="instagram">
      <icon name="instagram" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.twitch.tv/lowco" title="twitch" target="_blank" aria-label="twitch">
      <icon name="twitch" class="icon" :scale="iconSize"></icon>
    </a>
  </div>
</template>

<script>
export default {
  name: 'social-media-icons',
  data () {
    return {
      iconSize: 1.5
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.icon{
  fill: $font-white;
  vertical-align: bottom;
  margin: 0 $standard-spacing;
  transition: $standard-transition;
}
a{
  display: inline-block;
  &:hover, &:focus {
    transform: scale(1.2);
    .icon{
      fill: $light-purple;
    }
  }
}
@media (max-width: 500px){
  .icon{
    margin: 0 $standard-spacing-half;
  }
}
</style>



